.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 75vh;
  justify-content: space-between;
}

/* Estilos responsivos */
@media (max-width: 768px) {
  .App {
    padding: 1rem;
  }
  
  .App h1 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
  }
}